@tailwind base;
@tailwind components;
@tailwind utilities;

html,
body {
    padding: 0;
    margin: 0;
    font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen,
        Ubuntu, Cantarell, Fira Sans, Droid Sans, Helvetica Neue, sans-serif;
}

a {
    color: inherit;
    text-decoration: none;
}

* {
    box-sizing: border-box;
}

@font-face {
    font-family: "burtons";
    src: url("../public/Burtons.otf");
    /* font-family: "ptsans";
    src: url("https://fonts.googleapis.com/css2?family=PT+Sans+Narrow:wght@700&display=swap") */
}